import * as React from "react";
import * as RadioGroup from "@radix-ui/react-radio-group";
import { BvLabel } from "../bvLabel";

export interface BvRadioGroupProps {
  id: string;
  label: string;
  layout?: string | "horizontal" | "vertical";
  dir?: "ltr" | "rtl";
  defaultValue?: string;
  items: Array<{ value: string; label: string }>;
  onValueChange?: (value: string) => void;
}

function renderItems(
  id: BvRadioGroupProps["id"],
  layout: BvRadioGroupProps["layout"],
  items: BvRadioGroupProps["items"]
) {
  return items.map((item, i) => (
    <div
      key={id + i}
      className={`flex rtl:flex-row-reverse gap-1 items-center`}
    >
      <RadioGroup.Item
        id={id + i}
        value={item.value}
        className="bv-form-item-input h-5 w-5 rounded-full"
      >
        <RadioGroup.Indicator className="bv-form-item-radio-indicator" />
      </RadioGroup.Item>
      <BvLabel htmlFor={id + i} label={item.label} layout={layout} />
    </div>
  ));
}

export const BvRadioGroup = React.forwardRef(({
  defaultValue,
  layout,
  dir,
  id,
  label,
  items,
  onValueChange
}: BvRadioGroupProps, ref: any) => {
  return (
    <RadioGroup.Root
      ref={ref}
      id={id}
      defaultValue={defaultValue || ""}
      dir={dir}
      className={`py-2 flex gap-2 ${layout === "horizontal" ? "flex-row" : "flex-col"}`}
      onValueChange={onValueChange}
    >
      <BvLabel htmlFor={id} label={label} layout={layout} />
      {renderItems(id, layout, items)}
    </RadioGroup.Root>
  );
})
