import React from 'react'
import styles from './Docking.module.scss'
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../01_redux/store';
import {setViewSidePanelAC} from '../../00_redux/actions';


export const Docking: React.FC = () => {
    const dispatch = useDispatch()
    const viewSidePanel = useSelector<AppRootStateType, boolean>(state => state.scheduler.viewSidePanel)

    return (
        <div className={styles.main}>

            <button className={styles.button}
                    onClick={() => dispatch(setViewSidePanelAC(!viewSidePanel))}
            >
                Scheduling Series
            </button>

        </div>
    )
}


