import React from 'react'
import styles from './View.module.scss'
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../01_redux/store';
import {ViewDaysOfWeekType} from '../../00_redux/types';
import {setViewDaysAC, setViewDaysOfWeekAC, setViewTimeAC} from '../../00_redux/actions';


const TIMES = [1, 3, 5, 10, 15, 30, 60]
const DAYS = [1, 3, 7, 14, 30, 31]
const DAYS_OF_WEEK = ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su']


export const View: React.FC = () => {
    const dispatch = useDispatch()

    const viewTimeLineSize = useSelector<AppRootStateType, number>(state => state.scheduler.viewTimeLineSize)
    const viewDays = useSelector<AppRootStateType, number>(state => state.scheduler.viewDays)
    const viewDaysOfWeek = useSelector<AppRootStateType, ViewDaysOfWeekType>(state => state.scheduler.viewDaysOfWeek)


    const times = TIMES.map((time, i) =>
        <option key={i} value={time}>{time} min</option>)

    const days = DAYS.map((days, i) =>
        <option key={i} value={days}>{days} days</option>)

    const daysOfWeek = DAYS_OF_WEEK.map((day, i) => {
        return <div key={i}>
            <input id={DAYS_OF_WEEK[i]}
                   style={{marginRight: '3px'}}
                   type={'checkbox'}
                   checked={viewDaysOfWeek.includes(day)}
                   onChange={(e) => {
                       dispatch(setViewDaysOfWeekAC(e.currentTarget.checked
                               ? [...viewDaysOfWeek, day]
                               : viewDaysOfWeek.filter(item => item !== day)
                           )
                       )
                   }}
            />
            <label htmlFor={DAYS_OF_WEEK[i]} style={{marginRight: '5px'}}>{DAYS_OF_WEEK[i]}</label>
        </div>
    })


    return (
        <div className={styles.main}>

            <div className={styles.schedulerSettings}>

                <div className={styles.timeDays}>
                    Time/Days:
                    <select
                        style={{border: 'none', width: '75%', height: '23px', padding: '0', margin: '0'}}
                        value={viewTimeLineSize}
                        onChange={(e) => {
                            dispatch(setViewTimeAC(+e.currentTarget.value))
                        }}>
                        {times}
                    </select>

                    <select style={{border: 'none', width: '75%', height: '23px', padding: '0', margin: '0'}}
                            value={viewDays}
                            onChange={(e) => {
                                dispatch(setViewDaysAC(+e.currentTarget.value))
                            }}
                    >
                        {days}
                    </select>
                </div>


                <div className={styles.daysOfWeek}>
                    Days of Week:
                    <div className={styles.checkBoxes}>
                        {daysOfWeek}
                    </div>

                </div>


                <div className={styles.channel}>
                    Channel:
                    <select style={{border: 'none', width: '95%', height: '23px', padding: '0', margin: '0'}}>
                        <option value={'Test'}>Test</option>
                    </select>
                </div>
            </div>


            <div className={styles.viewSettings}>
                Settings
            </div>

        </div>
    )
}