import React from 'react';
import {BvButton, BvDoWPicker, BvInput, BvSelect} from 'bv-ng-components';
import {IconClipboardCheck, IconLayoutSidebar, IconListSearch, IconPalette, IconStepInto} from '@tabler/icons';
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../01_redux/store';
import {setActiveChannelAC, setBeginDateAC, setViewDaysOfWeekAC, setViewSidePanelAC} from '../../00_redux/actions';
import {ViewDaysOfWeekType} from '../../00_redux/types';


const channels = {
    channels: [
        {label: "Chan1", value: 1},
        {label: "Chan2", value: 2},
        {label: "Chan3", value: 3},
    ],
};

const times = {
    times: [
        {label: "1min", value: 1},
        {label: "3min", value: 3},
        {label: "5min", value: 5},
        {label: "10min", value: 10},
        {label: "15min", value: 15},
        {label: "30min", value: 30},
        {label: "60min", value: 60},
    ],
};

const dayColumns = {
    days: [
        {label: "1d", value: 1},
        {label: "3d", value: 3},
        {label: "7d", value: 7},
        {label: "14d", value: 14},
        {label: "30d", value: 30},
        {label: "31d", value: 31},
    ],
};

const views = {
    views: [
        {label: "View", value: "view1"},
        {label: "Default", value: "default"},
        {label: "Series", value: "series"},
        {label: "Episodes", value: "episodes"},
    ],
};


export function LinearScheduler() {
    const dispatch = useDispatch()

    const beginDate = useSelector<AppRootStateType, string>(state => state.scheduler.beginDate)
    const activeChannelId = useSelector<AppRootStateType, number>(state => state.scheduler.activeChannelId)
    const viewDaysOfWeek = useSelector<AppRootStateType, ViewDaysOfWeekType>(state => state.scheduler.viewDaysOfWeek)
    const viewSidePanel = useSelector<AppRootStateType, boolean>(state => state.scheduler.viewSidePanel)


    return (
        <div className="w-full flex flex-col">
            <div className="w-full flex flex-grow-0 gap-2 py-2 px-2">
                <BvButton
                    id="toggle-docked-list"
                    tabIndex={1}
                    label="List"
                    iconStart={<IconLayoutSidebar/>}
                    onClick={() => dispatch(setViewSidePanelAC(!viewSidePanel))}
                />

                <BvInput
                    id="date-start"
                    label="Start Date"
                    type="date"
                    hideLabel
                    defaultValue={beginDate}
                    onChange={e => dispatch(setBeginDateAC(e.target.value))}
                />

                <BvSelect
                    id="select-channel"
                    label="Channel"
                    hideLabel
                    value={activeChannelId}
                    options={channels}
                />

                <BvSelect
                    id="select-time-columns"
                    label="Time Columns"
                    hideLabel
                    value={15}
                    options={times}
                />
                <BvSelect
                    id="select-day-columns"
                    label="Day Columns"
                    hideLabel
                    value={14}
                    options={dayColumns}
                />

                <BvDoWPicker
                    id="select-dow"
                    label="Day of Week"
                    defaultValue={viewDaysOfWeek}
                    onValueChange={value => dispatch(setViewDaysOfWeekAC(value))}
                />

                <BvButton
                    id="find"
                    label="Find"
                    iconStart={<IconListSearch/>}
                />
                <BvButton
                    id="find"
                    label="Go To"
                    iconStart={<IconStepInto/>}
                />
                <BvButton
                    id="find"
                    label="Check"
                    iconStart={<IconClipboardCheck/>}
                />
                <BvSelect
                    id="select-view"
                    label="View"
                    hideLabel
                    value="view1"
                    options={views}
                />
                <BvButton
                    id="find"
                    label="Color"
                    iconStart={<IconPalette/>}
                />
            </div>

        </div>
    );
}