import React from 'react';
import styles from './SchedulerBody.module.scss';
import {SidePanel} from './01_SidePanel/SidePanel';
import {Table} from './02_Table/Table';
import {useSelector} from 'react-redux';
import {AppRootStateType} from '../../01_redux/store';

export const SchedulerBody: React.FC = () => {
    const viewSidePanel = useSelector<AppRootStateType, boolean>(state => state.scheduler.viewSidePanel)

    return (
        <div className={styles.main} id={'schedulerBody'}>
            {viewSidePanel && <SidePanel/>}
            <Table/>
        </div>
    )
}