import React from 'react'
import styles from './TableGrid.module.scss'
import {useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../../01_redux/store';
import {DaysListType, LoadedContainersType, TableContentParamsType} from '../../../../00_redux/types';

type TableGridType = {
    dragCoords: Array<number>
}


export const TableGrid: React.FC<TableGridType> = (props) => {
    const {
        tableCellXpx,
        tableCellYpx,
    } = useSelector<AppRootStateType, TableContentParamsType>(state => state.scheduler.tableContentParams)

    const viewTimeLineSize = useSelector<AppRootStateType, number>(state => state.scheduler.viewTimeLineSize)
    const daysList = useSelector<AppRootStateType, DaysListType>(state => state.scheduler.daysList)
    const loadedContainersList = useSelector<AppRootStateType, LoadedContainersType>(state => state.scheduler.loadedContainersList)
    const draggedContainerId = useSelector<AppRootStateType, string>(state => state.scheduler.draggedContainerId)

    const svgWidth = daysList.length * tableCellXpx


    return (
        <>
            {(props.dragCoords[0] !== -1) &&
                <div
                    style={{
                        position: 'absolute',
                        zIndex: '10',
                        left: `${props.dragCoords[0]}px`,
                        top: `${props.dragCoords[1]}px`,
                        width: `${tableCellXpx}px`,
                        // height: `${tableCellYpx}px`,
                        height: `${loadedContainersList[draggedContainerId].objectDuration / 60 * tableCellYpx / viewTimeLineSize}px`,
                        background: 'lime',
                    }}
                    // onDragOver={(e) => {
                    //     e.stopPropagation()
                    //     e.preventDefault()
                    // }}
                />
            }


            <svg width={svgWidth} height={24 * 60 / viewTimeLineSize * tableCellYpx}>
                <defs>
                    <pattern id={'border'} width={tableCellXpx} height={tableCellYpx} patternUnits={'userSpaceOnUse'}>
                        <line x1={tableCellXpx} y1={'0'} x2={tableCellXpx} y2={tableCellYpx} stroke={'blue'}/>
                        {/*<line x1={0} y1={'0'} x2={0} y2={tableCellYpx} stroke={'blue'}/>*/}
                    </pattern>

                    <pattern id={'background'} width={svgWidth} height={tableCellYpx * 2}
                             patternUnits={'userSpaceOnUse'}>
                        <rect x={'0'} y={'0'} width={svgWidth} height={tableCellYpx} fill={'lightcoral'}/>
                        {/*<rect x={'0'} y={tableCellYpx} width={svgWidth} height={tableCellYpx * 2} fill={'#E3E3E3'}/>*/}
                        <rect x={'0'} y={tableCellYpx} width={svgWidth} height={tableCellYpx * 2} fill={'yellow'}/>
                        <rect width={svgWidth} height={'100%'} fill={'url(#border)'}/>
                    </pattern>
                </defs>

                <rect width={svgWidth} height={'100%'} fill={'url(#background)'}/>

                {/*{(props.dragCoords[0] !== -1) &&*/}
                {/*    <rect*/}
                {/*        style={{position: 'absolute',zIndex: '10000'}}*/}
                {/*        x={props.dragCoords[0]} y={props.dragCoords[1]} width={tableCellXpx} height={tableCellYpx} fill={'lime'}/>*/}
                {/*}*/}
            </svg>
        </>
    )
}