import {
    DaysListType,
    GroupedContainersListType,
    LoadedContainerType,
    SelectedContainersListType, SelectWindowCoordsType,
    TableContentParamsType, ViewDaysOfWeekType
} from './types';
import {
    InitDataTypeTableContainers
} from '../02_SchedulerBody/02_Table/02_TableBody/03_TableContainers/TableContainers';

export enum SchedulerActionsType {
    LOAD_TABLE_CONTENT_PARAMS = 'SCHEDULER/LOAD_TABLE_CONTENT_PARAMS',

    SET_BEGIN_DATE = 'SCHEDULER/SET_BEGIN_DATE',

    SET_ACTIVE_CHANNEL = 'SCHEDULER/SET_ACTIVE_CHANNEL',

    SET_DAYS_LIST = 'SCHEDULER/SET_DAYS_LIST',

    SET_VIEW_TIME = 'SCHEDULER/SET_VIEW_TIME',
    SET_VIEW_DAYS = 'SCHEDULER/SET_VIEW_DAYS',
    SET_VIEW_DAYS_OF_WEEK = 'SCHEDULER/SET_VIEW_DAYS_OF_WEEK',

    MOVE_CONTAINER_TO_NEW_POSITION = 'SCHEDULER/MOVE_CONTAINER_TO_NEW_POSITION',

    INIT_GROUPED_CONTAINER_LIST = 'SCHEDULER/INIT_GROUPED_CONTAINER_LIST',

    SET_VIEW_SIDE_PANEL = 'SCHEDULER/SET_VIEW_SIDE_PANEL',

    SET_DRAG_START_SIDE_PANEL = 'SCHEDULER/SET_DRAG_START_SIDE_PANEL',

    ADD_NEW_CONTAINER = 'SCHEDULER/ADD_NEW_CONTAINER',
    DELETE_CONTAINER = 'SCHEDULER/DELETE_CONTAINER',
    DELETE_CONTAINERS_GROUP = 'SCHEDULER/DELETE_CONTAINERS_GROUP',

    SET_CONTEXT_MENU = 'SCHEDULER/SET_CONTEXT_MENU',

    UNSELECT_ALL_CONTAINERS = 'SCHEDULER/UNSELECT_ALL_CONTAINERS',
    SET_CONTAINER_SELECTION = 'SCHEDULER/SET_CONTAINER_SELECTION',
    CHOOSE_CONTAINERS_BY_SELECT_WINDOW = 'SCHEDULER/CHOOSE_CONTAINERS_BY_SELECT_WINDOW',
    CHOOSE_CONTAINERS_BY_DATE = 'SCHEDULER/CHOOSE_CONTAINERS_BY_DATE',

    SET_CONTAINERS_DRAG_MODE = 'SCHEDULER/SET_CONTAINERS_DRAG_MODE',

    CHANGE_DATE_TIME_CONTAINERS = 'SCHEDULER/CHANGE_DATE_TIME_CONTAINERS',


}


export const loadTableContentParamsAC = (params: TableContentParamsType) =>
    ({type: SchedulerActionsType.LOAD_TABLE_CONTENT_PARAMS, params} as const)


export const setBeginDateAC = (date: string) =>
    ({type: SchedulerActionsType.SET_BEGIN_DATE, date} as const)


export const setActiveChannelAC = (channelId: number) =>
    ({type: SchedulerActionsType.SET_ACTIVE_CHANNEL, channelId} as const)


export const setDaysListAC = (days: DaysListType) =>
    ({type: SchedulerActionsType.SET_DAYS_LIST, days} as const)


export const setViewTimeAC = (time: number) =>
    ({type: SchedulerActionsType.SET_VIEW_TIME, time} as const)
export const setViewDaysAC = (days: number) =>
    ({type: SchedulerActionsType.SET_VIEW_DAYS, days} as const)
// export const setViewDaysOfWeekAC = (dayOfWeek: number, value: boolean) =>
//     ({type: SchedulerActionsType.SET_VIEW_DAYS_OF_WEEK, dayOfWeek, value} as const)
export const setViewDaysOfWeekAC = (daysOfWeek: ViewDaysOfWeekType) =>
    ({type: SchedulerActionsType.SET_VIEW_DAYS_OF_WEEK, daysOfWeek} as const)


// export const moveContainerToNewPositionAC = (containerIndex: number, newDateIndex: number, newTime: number) =>
//     ({type: SchedulerActionsType.MOVE_CONTAINER_TO_NEW_POSITION, containerIndex, newDateIndex, newTime} as const)
export const moveContainerToNewPositionAC = (containerId: string, newDate: string, newTime: number) =>
    ({type: SchedulerActionsType.MOVE_CONTAINER_TO_NEW_POSITION, containerId, newDate, newTime} as const)


export const initGroupedContainersListAC = (groupedContainersList: GroupedContainersListType) =>
    ({type: SchedulerActionsType.INIT_GROUPED_CONTAINER_LIST, groupedContainersList} as const)


export const setViewSidePanelAC = (value: boolean) =>
    ({type: SchedulerActionsType.SET_VIEW_SIDE_PANEL, value} as const)


export const setDragStartSidePanelAC = (value: boolean, draggedContainerId: string) =>
    ({type: SchedulerActionsType.SET_DRAG_START_SIDE_PANEL, value, draggedContainerId} as const)


export const addNewContainerAC = (containerId: string, container: LoadedContainerType, date: string, time: number, chanelID: number) =>
    ({type: SchedulerActionsType.ADD_NEW_CONTAINER, containerId, container, date, time, chanelID} as const)
export const deleteContainerAC = (containerId: string) =>
    ({type: SchedulerActionsType.DELETE_CONTAINER, containerId} as const)
export const deleteContainersGroupAC = (containersGroup: SelectedContainersListType) =>
    ({type: SchedulerActionsType.DELETE_CONTAINERS_GROUP, containersGroup} as const)


export const setContextMenuAC = (contextMenuName: string, value: boolean) =>
    ({type: SchedulerActionsType.SET_CONTEXT_MENU, contextMenuName, value} as const)


export const unselectAllContainersAC = () =>
    ({type: SchedulerActionsType.UNSELECT_ALL_CONTAINERS} as const)
export const setContainerSelectionAC = (containerId: string, value: boolean) =>
    ({type: SchedulerActionsType.SET_CONTAINER_SELECTION, containerId, value} as const)
export const chooseContainersBySelectWindowAC = (ctrlKey: boolean, startSelectWindowCoords: SelectWindowCoordsType, currentSelectWindowCoords: SelectWindowCoordsType, initData: InitDataTypeTableContainers) =>
    ({
        type: SchedulerActionsType.CHOOSE_CONTAINERS_BY_SELECT_WINDOW,
        ctrlKey,
        startSelectWindowCoords,
        currentSelectWindowCoords,
        initData
    } as const)
export const chooseContainersByDateAC = (ctrlKey: boolean, date: string) =>
    ({type: SchedulerActionsType.CHOOSE_CONTAINERS_BY_DATE, ctrlKey, date} as const)


export const setContainersDragModeAC = (value: boolean) =>
    ({type: SchedulerActionsType.SET_CONTAINERS_DRAG_MODE, value} as const)


export const changeDateTimeContainersAC = (containers: SelectedContainersListType, deltaDays: number, deltaMinutes: number) =>
    ({type: SchedulerActionsType.CHANGE_DATE_TIME_CONTAINERS, containers, deltaDays, deltaMinutes} as const)





export type Actions =
    | ReturnType<typeof loadTableContentParamsAC>
    | ReturnType<typeof setBeginDateAC>
    | ReturnType<typeof setActiveChannelAC>
    | ReturnType<typeof setDaysListAC>
    | ReturnType<typeof setViewTimeAC>
    | ReturnType<typeof setViewDaysAC>
    | ReturnType<typeof setViewDaysOfWeekAC>
    | ReturnType<typeof moveContainerToNewPositionAC>
    | ReturnType<typeof initGroupedContainersListAC>
    | ReturnType<typeof setViewSidePanelAC>
    | ReturnType<typeof setDragStartSidePanelAC>
    | ReturnType<typeof addNewContainerAC>
    | ReturnType<typeof deleteContainerAC>
    | ReturnType<typeof deleteContainersGroupAC>
    | ReturnType<typeof setContextMenuAC>
    | ReturnType<typeof unselectAllContainersAC>
    | ReturnType<typeof setContainerSelectionAC>
    | ReturnType<typeof chooseContainersBySelectWindowAC>
    | ReturnType<typeof chooseContainersByDateAC>
    | ReturnType<typeof setContainersDragModeAC>
    | ReturnType<typeof changeDateTimeContainersAC>


