import {Dispatch} from 'redux';
import {AppRootStateType} from '../../01_redux/store';
import moment from 'moment';
import {DaysListType, GroupedContainersListType} from './types';
import {initGroupedContainersListAC, moveContainerToNewPositionAC, setDaysListAC} from './actions';

export const generateDaysListTC = () => (dispatch: Dispatch, getState: () => AppRootStateType) => {
    const state = getState().scheduler

    //  
    let days: DaysListType = [];
    // for (let i = 0; i < state.viewDays; i++) {

    // const numberDaysOfWeek = state.viewDaysOfWeek.reduce((acc, el) => acc + +el, 0)

    if (state.beginDate && state.viewDaysOfWeek.length) {
        let i = 0

        while (days.length < state.viewDays) {
            // let day = moment().startOf('isoWeek').add(i, "d").format('YYYY-MM-DD')
            let day = moment(state.beginDate).add(i, "d").format('YYYY-MM-DD')


            // let dayOfWeek = +moment(day, 'YYYY-MM-DD').format('d')
            // dayOfWeek = dayOfWeek === 0
            //     ? 6
            //     : dayOfWeek - 1
            //
            // if (state.viewDaysOfWeek[dayOfWeek]) {
            //     days.push(day)
            // }

            let dayOfWeek = moment(day, 'YYYY-MM-DD').format('dd')
            if (state.viewDaysOfWeek.includes(dayOfWeek)) {
                days.push(day)
            }

            i++
        }
    }

    dispatch(setDaysListAC(days))
}


export const initGroupedContainersListTC = () => (dispatch: Dispatch, getState: () => AppRootStateType) => {

    // alert('!!! Press F12 !!!')

    const state = getState().scheduler
    const activeChannelId = state.activeChannelId
    const containersList = state.containersList

    // const containersDaysList = {
    //     "2022-02-28": ["uuid1", "uuid1_2", "uuid1_1", "uuid2"],
    //     "2022-03-03": ["uuid2", "uuid2_1", "uuid2_2", "uuid4"],
    //     "2022-03-04": ["uuid2_3", "uuid5"],
    //     "2022-03-02": ["uuid3"],
    // }


    const containersDaysList: { [date: string]: Array<string> } = {}
    Object.keys(containersList).forEach((containerId) => {
        if (containersList[containerId].chanelID === activeChannelId) {
            const date = containersList[containerId].logDate
            if (containersDaysList[date] === undefined) {
                containersDaysList[date] = [containerId]
            } else {
                containersDaysList[date].push(containerId)
            }
        }
    })


    const isIntersected = (id1: string, id2: string) => {
        const containerStartTime = containersList[id1].startTime * 60
        const containerEndTime = containerStartTime + containersList[id1].objectDuration - 1

        const otherContainerStartTime = containersList[id2].startTime * 60
        const otherContainerEndTime = otherContainerStartTime + containersList[id2].objectDuration - 1

        if ((otherContainerStartTime >= containerStartTime && otherContainerStartTime <= containerEndTime)
            || (otherContainerEndTime >= containerStartTime && otherContainerEndTime <= containerEndTime)
            || (otherContainerStartTime <= containerStartTime && otherContainerEndTime >= containerEndTime)) {
            return true
        }

        return false
    }

    const findIntersection = (elements: Array<string>, where: Array<string>): Array<string> => {
        // debugger
        if (where.length === 0) {
            return elements
        }

        const res: Array<string> = []
        elements.forEach(el => {
            if (where.length > 0) {
                const r = where.filter(whEl => isIntersected(el, whEl))
                where = where.filter(whEl => r.indexOf(whEl) === -1)
                res.push(...r)
            }
        })

        if (res.length === 0) {
            return elements
        }
        return [...elements, ...findIntersection(res, where)]
    }


    const groupedContainersList: GroupedContainersListType = {}

    Object.keys(containersDaysList).forEach((date) => {
        const result: Array<Array<string>> = []
        let oneDayArray = containersDaysList[date]
        while (oneDayArray.length > 1) {
            const res = findIntersection([oneDayArray[0]], oneDayArray.slice(1))

            // ===========================================================
            // SORT containers into group
            //

            // console.log('UNsorted', res)
            res.sort((a, b) => containersList[a].startTime - containersList[b].startTime)

            // res.sort((a, b) => {
            //     if (containersList[a].startTime < containersList[b].startTime) {
            //         return -1
            //     } else if (containersList[a].startTime > containersList[b].startTime) {
            //         return 1
            //     } else {
            //         if (res.indexOf(a) < res.indexOf(b)) return -1
            //         return 1
            //     }
            // })

            // containersList[a].startTime < containersList[b].startTime
            //     ? -1
            //     : 1)
            // console.log('sorted',res)

            result.push(res)
            oneDayArray = oneDayArray.filter(el => res.indexOf(el) === -1)
        }
        if (oneDayArray.length === 1) {
            result.push(oneDayArray)
        }

        groupedContainersList[date] = result
    })


    dispatch(initGroupedContainersListAC(groupedContainersList))
}


// !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
//
// dispatch: Dispatch<any> - for Thunk !!! Change ANY for correct type !!!
//
export const moveContainerToNewPositionTC = (containerId: string, newDate: string, newTime: number) => (dispatch: Dispatch<any>, getState: () => AppRootStateType) => {
    dispatch(moveContainerToNewPositionAC(containerId, newDate, newTime))
    dispatch(initGroupedContainersListTC())
}


