import React from "react";

export interface BvButtonProps {
    id: string;
    type?: "button" | "submit" | "reset";
    primary?: boolean,
    autoFocus?: boolean,
    tabIndex?: number,
    disabled?: boolean,
    label: string;
    hideLabel?: boolean;
    iconStart?: React.ReactNode;
    iconEnd?: React.ReactNode;
    onClick?: () => void;
    className?: string;

}

export const BvButton = ({ id, autoFocus, tabIndex, label, hideLabel, disabled, iconStart, iconEnd, onClick, className, primary, type }: BvButtonProps) => {
    return (
        <button id={id} autoFocus={autoFocus} tabIndex={tabIndex} disabled={disabled} aria-label={label} onClick={onClick} type={type || 'button'} className={`${primary ? 'btn-primary' : 'btn-secondary'} ${disabled ? 'btn-disabled' : ""} ${className ? className : ""}`}>
            {iconStart}
            {!hideLabel ? label : null}
            {iconEnd}
        </button>
    );
}

