import React from "react";
import styles from './index.module.scss'

export interface BvLinearEventProps {
    id: string;
    primaryText: string;
    secondaryText: string;
    tagText: string;
    objectType: string;
    objectEpisodeInfo: string;
    objectDuration: string;
    height: number;
    width: number;
    color: string;

    // =======
    selected: boolean
    // containersSelectable: boolean
    // =======
}

export const BvLinearEvent = React.forwardRef(({

                                                   id,
                                                   primaryText,
                                                   secondaryText,
                                                   tagText,
                                                   objectType,
                                                   objectEpisodeInfo,
                                                   objectDuration,
                                                   width,
                                                   height,
                                                   color,

                                                   selected,
                                                   // containersSelectable,

                                               }: BvLinearEventProps, ref: any) => {

    // const selectStyle = containersSelectable
    //     ? styles.selectable
    //     : styles.unselectable

    return (
        <div id={id}
            // className={selectStyle + " flex flex-col border-2 overflow-hidden border-slate-300 rounded-md text-slate-900 text-sm leading-tight tracking-tight bg-gradient-to-b from-slate-50 to-sky-300"}
             className="flex flex-col border-2 overflow-hidden border-slate-300 rounded-md text-slate-900 text-sm leading-tight tracking-tight bg-gradient-to-b from-slate-50 to-sky-300"
             style={{
                 width: width,
                 height: height,
                 backgroundColor: color,
                 border: `${selected ? '2px dashed red' : 'none'}`,
                 // position: 'relative',
             }}
        >

            <div className="flex flex-col justify-between flex-grow flex-shrink overflow-hidden">
                <div>
                    <div className="p-2 font-semibold text-lg w-full truncate">{primaryText}</div>
                    <div className="px-2 truncate">{secondaryText}</div>
                </div>
                <div className="px-2 py-2 w-full flex"><span
                    className="bg-slate-400 mix-blend-multiply rounded-md w-full py-1 px-2 text-white font-semibold truncate">{tagText}</span>
                </div>
            </div>
            <div
                className="flex justify-between overflow-hidden py-1 px-2 text-xs font-semibold border-collapse bg-slate-50/70">
                <span>{objectType}</span>
                <span>{objectEpisodeInfo}</span>
                <span>{objectDuration}</span>
            </div>
        </div>
    );
})


