import React from 'react';
import * as ToggleGroup from '@radix-ui/react-toggle-group';
import { BvButton } from '../bvButton';
import { IconChevronDown } from '@tabler/icons';

// DOW_INDEX is taken from the Sales Module implementation
/* const DOW_INDEX = {
    su: 0,
    mo: 1,
    tu: 2,
    we: 3,
    th: 4,
    fr: 5,
    sa: 6,
};
 */
// DEFAULT_WEEKDAYS_TEXT is taken from the Sales Module implementation
const DEFAULT_WEEKDAYS_TEXT = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];

export interface BvDoWPickerProps {
    id: string;
    label: string;
    /* dayStart?: number; */
    disabledDays?: string[];
    onValueChange?: (value: string[]) => void;
    defaultValue?: string[];
    disabled?: boolean;
}

function renderDays(/* dowIndex, startDay:string, */ dayNames: string[], disabledDays: string[] = []) {
    const days: any = [];


    DEFAULT_WEEKDAYS_TEXT.map((day, index) => {
        days.push(
            <ToggleGroup.Item
                key={index}
                disabled={disabledDays ? disabledDays.includes(day) : false}
                value={day}
                className="inline-flex   w-9 items-center justify-center p-1 bg-slate-50 rounded-none first:rounded-l-md last:rounded-r-md hover:bg-slate-100 focus:shadow-none focus:outline-none focus:ring focus:ring-sky-400/50 focus:border-sky-500 focus:rounded-sm disabled:bg-slate-300 disabled:text-slate-500"
            >
                {dayNames[index]}
            </ToggleGroup.Item>
        );
    });
    return days;
}

//TODO: add a way to choose all days, weekdays, or all weekends
//TODO: research sales module implementation to understand bit mask logic. We should support that too.

export const BvDoWPicker = React.forwardRef(
    (
        {
            id,
            /*   dayStart, */
            disabledDays,
            onValueChange,
            defaultValue,
            disabled,
        }: BvDoWPickerProps,
        ref: any
    ) => {



        return (
            <ToggleGroup.Root
                id={id}
                ref={ref}
                disabled={disabled}
                onValueChange={onValueChange}
                defaultValue={defaultValue}
                type="multiple"
                className="bv-dow-picker inline-flex items-center justify-items-center gap-0.75 py-1 h-9 bg-slate-200 text-slate-900 rounded-md border-2 border-solid border-slate-300 "
            >
                {renderDays(/* DOW_INDEX, */ DEFAULT_WEEKDAYS_TEXT, disabledDays)}
                <BvButton id="dow-shortcuts" disabled={disabled} label="Shortcuts" hideLabel={true} iconEnd={<IconChevronDown />} className="rounded-l-none border-0 w-8 h-8" />
            </ToggleGroup.Root>
        );
    }
);
