import React, {MouseEvent, useEffect, useState} from 'react'
import styles from './TableContainers.module.scss'
import './TableContainersDraggable.scss'
import {
    ContainerListType, ContextMenusNames, ContextMenusType,
    DaysListType,
    GroupedContainersListType, SelectedContainersListType,
    TableContentParamsType
} from '../../../../00_redux/types';
import Draggable, {DraggableData, DraggableEvent} from 'react-draggable';
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../../01_redux/store';
import {
    deleteContainerAC, unselectAllContainersAC,
    loadTableContentParamsAC,
    setContainerSelectionAC,
    setContextMenuAC, deleteContainersGroupAC, setContainersDragModeAC, changeDateTimeContainersAC
} from '../../../../00_redux/actions';
import {BvLinearEvent} from './01_bvLinearEvent';
import {initGroupedContainersListTC, moveContainerToNewPositionTC} from '../../../../00_redux/thunks';
import {ContextMenu} from '../../../../../00_common/02_components/01_ContextMenu/ContextMenu';


const COLUMN_RIGHT_BORDER_WIDTH = 1
const CONTAINERS_GAP = 2


export type InitDataTypeTableContainers = {
    [id: string]: {
        xy: {
            x: number
            y: number
        }
        width: number
    }
}


type TableContainersPropsType = {
    setInitDataForColumns: (data: InitDataTypeTableContainers) => void
    containersSelectable: boolean
}


export const TableContainers: React.FC<TableContainersPropsType> = (props) => {

    const dispatch = useDispatch()
    const {
        viewWidth,
        contentWidth,
        scrollLeft,
        tableCellXpx,
        tableCellYpx,
    } = useSelector<AppRootStateType, TableContentParamsType>(state => state.scheduler.tableContentParams)
    const viewTimeLineSize = useSelector<AppRootStateType, number>(state => state.scheduler.viewTimeLineSize)
    const daysList = useSelector<AppRootStateType, DaysListType>(state => state.scheduler.daysList)
    const containersList = useSelector<AppRootStateType, ContainerListType>(state => state.scheduler.containersList)
    const groupedContainersList = useSelector<AppRootStateType, GroupedContainersListType>(state => state.scheduler.groupedContainersList)
    const contextMenus = useSelector<AppRootStateType, ContextMenusType>(state => state.scheduler.contextMenus)
    const selectedContainersList = useSelector<AppRootStateType, SelectedContainersListType>(state => state.scheduler.selectedContainersList)
    const containersDragMode = useSelector<AppRootStateType, boolean>(state => state.scheduler.containersDragMode)

    // console.log('TableContainers:', selectedContainersList)

    // ==================================================================================

    const [initData, setInitData] = useState<InitDataTypeTableContainers>({})

    useEffect(() => {
        const initDataObj: InitDataTypeTableContainers = {}
        daysList.forEach((date, dateIndex) => {
            groupedContainersList[date]?.forEach(array => {

                const width = (tableCellXpx - COLUMN_RIGHT_BORDER_WIDTH - (array.length + 1) * CONTAINERS_GAP) / array.length
                array.forEach((id, pos) => {
                    const container = containersList[id]

                    initDataObj[id] = {
                        xy: {
                            x: CONTAINERS_GAP + tableCellXpx * dateIndex + pos * (CONTAINERS_GAP + width),
                            y: container.startTime * tableCellYpx / viewTimeLineSize,
                        },
                        width: width,
                    }
                })

            })
        })

        setInitData(initDataObj)
        props.setInitDataForColumns(initDataObj)
    }, [daysList, groupedContainersList, tableCellXpx, tableCellYpx, viewTimeLineSize])
    // ==================================================================================


    // ==================================================================================

    const NEVER_SCREEN_SIZE = 1000000

    const TIMEOUT_INTERVAL = 20
    const MAX_TIMEOUT_COUNTER = 15
    const [selectTimeoutCounter, setSelectTimeoutCounter] = useState<number>(0)
    const [selectTimeoutId, setSelectTimeoutId] = useState<NodeJS.Timeout>()

    const [leftMouseDownStart, setLeftMouseDownStart] = useState<boolean>(false)

    const [mouseLeaveContainer, setMouseLeaveContainer] = useState<boolean>(false)

    const [groupMoveContainerId, setGroupMoveContainerId] = useState<string>('')

    type GroupFrameType = {
        left: number
        top: number
        width: number
        height: number
        topDeltaMinutes: number
    }
    const [groupFrame, setGroupFrame] = useState<GroupFrameType>({
        left: -1,
        top: -1,
        width: -1,
        height: -1,
        topDeltaMinutes: -1,
    })


    const timeoutHandler = (data: DraggableData, containerId: string) => {
        let mouseLeaveContainerFromState = false
        setMouseLeaveContainer(state => {
            mouseLeaveContainerFromState = state
            return mouseLeaveContainerFromState
        })
        console.log('mouseLeaveContainerFromState', mouseLeaveContainerFromState)
        if (mouseLeaveContainerFromState) {
            return
        }

        let selectTimeoutCounterFromState = 0
        setSelectTimeoutCounter(state => {
            selectTimeoutCounterFromState = state + 1
            return selectTimeoutCounterFromState
        })
        console.log('selectTimeoutCounterFromState', selectTimeoutCounterFromState)

        if (selectTimeoutCounterFromState < MAX_TIMEOUT_COUNTER) {
            const timerId = setTimeout(() => timeoutHandler(data, containerId), TIMEOUT_INTERVAL)
            setSelectTimeoutId(timerId)
        } else {
            console.log('cursor = move')

            // dispatch(unselectAllContainersAC())

            dispatch(setContainersDragModeAC(true))

            if (!selectedContainersList[containerId] || Object.keys(selectedContainersList).length <= 1) {
                dispatch(unselectAllContainersAC())

                setInitData(state =>
                    ({
                        ...state,
                        [containerId]: {
                            xy: {
                                x: CONTAINERS_GAP + Math.trunc(data.x / tableCellXpx) * tableCellXpx,
                                y: Math.trunc(data.y / tableCellYpx) * tableCellYpx,
                            },
                            width: tableCellXpx - 2 * CONTAINERS_GAP - COLUMN_RIGHT_BORDER_WIDTH,
                        },
                    })
                )

            } else {

                // Group move

                const group = Object.keys(selectedContainersList).reduce((result, containerId) => {
                    const leftCurrent = initData[containerId]?.xy.x
                    const leftResult = result.left
                    const topCurrent = initData[containerId]?.xy.y
                    const topResult = result.top
                    const rightCurrent = initData[containerId]?.xy.x + initData[containerId]?.width
                    const rightResult = result.right
                    const bottomCurrent = initData[containerId]?.xy.y + containersList[containerId]?.objectDuration / 60 * tableCellYpx / viewTimeLineSize
                    const bottomResult = result.bottom

                    return {
                        left: leftResult === -1 || leftCurrent < leftResult ? leftCurrent : leftResult,
                        top: topResult === -1 || topCurrent < topResult ? topCurrent : topResult,
                        right: rightResult === -1 || rightCurrent > rightResult ? rightCurrent : rightResult,
                        bottom: bottomResult === -1 || bottomCurrent > bottomResult ? bottomCurrent : bottomResult,
                        topDeltaMinutes: topResult === -1 || topCurrent < topResult
                            ? Math.trunc(containersList[containerId].startTime / viewTimeLineSize) * viewTimeLineSize - containersList[containerId].startTime
                            : result.topDeltaMinutes,

                    }
                }, {left: -1, top: -1, right: -1, bottom: -1, topDeltaMinutes: -1})

                const groupGridLeft = Math.trunc(group.left / tableCellXpx) * tableCellXpx
                const groupGridTop = Math.trunc(group.top / tableCellYpx) * tableCellYpx
                const groupGrid = {
                    left: groupGridLeft,
                    top: groupGridTop,
                    width: Math.ceil(group.right / tableCellXpx) * tableCellXpx - groupGridLeft,
                    height: group.bottom - group.top + 1,
                    topDeltaMinutes: group.topDeltaMinutes,
                }
                setGroupFrame(groupGrid)

                console.log('Group containers frame coords:', group)
                console.log('Group containers frame (GRID):', groupGrid)

                setGroupMoveContainerId(containerId)
            }

        }
    }
    // =================================================

    const onStartHandler = (e: DraggableEvent, data: DraggableData, containerId: string) => {
        if (!e.shiftKey) {
            console.log('===drag Start===')
            e.stopPropagation()

            dispatch(setContextMenuAC('', false))

            setLeftMouseDownStart(true)
            setMouseLeaveContainer(false)
            setSelectTimeoutCounter(0)
            const timerId = setTimeout(() => timeoutHandler(data, containerId), TIMEOUT_INTERVAL)
            setSelectTimeoutId(timerId)
        }
    }


    const onDragHandler = (e: DraggableEvent, data: DraggableData, containerId: string) => {
        console.log('===drag===')
        // console.log('LastX=', data.lastX, 'deltaX=', data.deltaX, 'x=', data.x)
        // console.log('===containersDragMode',containersDragMode)

        // console.log('viewWidth + scrollLeft=', viewWidth + scrollLeft,
        //     'contentWidth=', contentWidth,
        //     'data.x=', data.x,
        //     'viewWidth + scrollLeft=', viewWidth + scrollLeft)

        // if (groupMoveContainerId) {
        //     if (viewWidth + scrollLeft < contentWidth && data.x + groupFrame.width >= viewWidth + scrollLeft) {
        //         dispatch(loadTableContentParamsAC({
        //             scrollLeft: Math.trunc((viewWidth + scrollLeft + tableCellXpx) / tableCellXpx) * tableCellXpx - viewWidth,
        //         } as TableContentParamsType))
        //     }
        // } else {
        //     if (viewWidth + scrollLeft < contentWidth && data.x >= viewWidth + scrollLeft) {
        //         dispatch(loadTableContentParamsAC({
        //             scrollLeft: Math.trunc((viewWidth + scrollLeft + tableCellXpx) / tableCellXpx) * tableCellXpx - viewWidth,
        //         } as TableContentParamsType))
        //     }
        // }

        if ((groupMoveContainerId &&
                viewWidth + scrollLeft < contentWidth &&
                data.x + groupFrame.width >= viewWidth + scrollLeft)
            ||
            (viewWidth + scrollLeft < contentWidth && data.x >= viewWidth + scrollLeft)) {
            dispatch(loadTableContentParamsAC({
                scrollLeft: Math.trunc((viewWidth + scrollLeft + tableCellXpx) / tableCellXpx) * tableCellXpx - viewWidth,
            } as TableContentParamsType))
        }

        if (scrollLeft > 0 && data.x < scrollLeft) {
            dispatch(loadTableContentParamsAC({
                scrollLeft: Math.trunc((scrollLeft - tableCellXpx) / tableCellXpx) * tableCellXpx,
            } as TableContentParamsType))
        }
    }


    const onStopHandler = (e: DraggableEvent, data: DraggableData, containerId: string) => {
        console.log('===drag Stop===')
        // console.log('LastX=', data.lastX, 'deltaX=', data.deltaX, 'x=', data.x)

        if (e.shiftKey) {
            return
        }

        selectTimeoutId && clearTimeout(selectTimeoutId)
        dispatch(setContainersDragModeAC(false))

        if (selectTimeoutCounter < MAX_TIMEOUT_COUNTER) {
            // SHORT press
            console.log('=shortPress')

            if (mouseLeaveContainer) {
                dispatch(unselectAllContainersAC())
                return
            }

            if (e.ctrlKey) {
                // Ctrl pressed

                if (selectedContainersList[containerId]) {
                    dispatch(setContainerSelectionAC(containerId, false))
                } else {
                    dispatch(setContainerSelectionAC(containerId, true))
                }
            } else {
                // no Ctrl pressed
                if (selectedContainersList[containerId]) {
                    dispatch(unselectAllContainersAC())
                    if (Object.keys(selectedContainersList).length > 1) {
                        dispatch(setContainerSelectionAC(containerId, true))
                    }
                } else {
                    dispatch(unselectAllContainersAC())
                    dispatch(setContainerSelectionAC(containerId, true))
                }
            }

            
        } else {
            // LONG press
            console.log('=longPress')

            if (groupMoveContainerId) {
                setGroupMoveContainerId('')

                // console.log(data.lastY, initData[containerId].xy.y, Math.ceil(Math.abs(data.lastY - initData[containerId].xy.y) * viewTimeLineSize / tableCellYpx))

                dispatch(changeDateTimeContainersAC(
                    selectedContainersList,
                    Math.trunc(data.lastX / tableCellXpx) - Math.trunc(groupFrame.left / tableCellXpx),
                    Math.trunc((data.lastY - groupFrame.top) / tableCellYpx) * viewTimeLineSize + groupFrame.topDeltaMinutes
                ))
                dispatch(initGroupedContainersListTC())
            } else {
                dispatch(unselectAllContainersAC())
                dispatch(moveContainerToNewPositionTC(containerId, daysList[Math.trunc(data.lastX / tableCellXpx)], Math.trunc((data.lastY / tableCellYpx) * viewTimeLineSize)))
            }
        }

        setLeftMouseDownStart(false)
    }
    // ==================================================================================


    // ==================================================================================
    const [contextMenuCoords, setContextMenuCoords] = useState<Array<number>>([-1, -1])
    const [containerIdForDelete, setContainerIdForDelete] = useState<string>('')

    const onContainerContextMenuHandler = (e: MouseEvent<HTMLDivElement>, id: string) => {
        e.preventDefault()
        e.stopPropagation()

        setContextMenuCoords([
            e.clientX + 5,
            e.clientY - 10,
        ])

        setContainerIdForDelete(id)

        dispatch(setContextMenuAC(ContextMenusNames.CONTAINER_CONTEXT_MENU, true))
    }

    const onClickContextMenuDelete = (e: MouseEvent<HTMLDivElement>) => {
        // e.stopPropagation()
        if (containerIdForDelete !== '') {
            dispatch(deleteContainerAC(containerIdForDelete))
            setContainerIdForDelete('')
            dispatch(initGroupedContainersListTC())
        }
    }

    const onClickContextMenuDeleteSelected = (e: MouseEvent<HTMLDivElement>) => {
        // e.stopPropagation()
        dispatch(deleteContainersGroupAC(selectedContainersList))
        dispatch(initGroupedContainersListTC())
    }
    // ==================================================================================

    const selectStyle = props.containersSelectable
        ? styles.selectable
        : styles.unselectable

    const tableContainers: Array<JSX.Element> = []
    daysList.forEach((date, dateIndex) => {
        // groupedContainersList[date]?.forEach(array => {
        if (groupedContainersList[date]) {
            groupedContainersList[date].forEach(groupArray => {

                groupArray.forEach((id, pos) => {

                    const container = containersList[id]

                    tableContainers.push(
                        <Draggable key={id}
                                   bounds={'parent'}
                                   onStart={(e, data) => onStartHandler(e, data, id)}
                                   onDrag={(e, data) => onDragHandler(e, data, id)}
                                   onStop={(e, data) => onStopHandler(e, data, id)}
                                   grid={containersDragMode
                                       ? [tableCellXpx, tableCellYpx]
                                       : [NEVER_SCREEN_SIZE, NEVER_SCREEN_SIZE]}
                            // position={initData[id] ? initData[id].xy : {x: -1, y: -1}}
                                   position={id === groupMoveContainerId
                                       ? {x: groupFrame.left, y: groupFrame.top}
                                       : initData[id] ? initData[id].xy : {x: -1, y: -1}}>

                            {/*<div className={styles.container}*/}
                            <div className={styles.container + ' ' + selectStyle}
                                 style={{cursor: `${containersDragMode ? 'move' : 'default'}`}}
                                 onContextMenu={(e) => onContainerContextMenuHandler(e, id)}
                                 onClick={e => e.stopPropagation()}
                                 onMouseLeave={() => {
                                     if (leftMouseDownStart && !mouseLeaveContainer) {
                                         console.log('===mouseLeaveContainer')
                                         setMouseLeaveContainer(true)
                                     }
                                 }}
                            >

                                {/*==========================================================*/}
                                {/*                       */}
                                {/* Group selection frame */}
                                {/*                       */}
                                {(groupMoveContainerId && id === groupMoveContainerId) &&
                                    <div className={styles.selectable}
                                         style={{
                                             // width: '100px',
                                             // height: '100px',
                                             // =======
                                             // height: `${container.objectDuration * tableCellYpx / (viewTimeLineSize * 60)}px`,
                                             // width: `${initData[id] ? initData[id].width : 0}px`,
                                             // =======
                                             // position: 'absolute',
                                             // left: `${groupFrame.left - initData[id].xy.x}px`,
                                             // top: `${groupFrame.top - initData[id].xy.y}px`,
                                             width: `${groupFrame.width}px`,
                                             height: `${groupFrame.height}px`,
                                             // =======
                                             background: 'blue',
                                             color: 'blue',
                                             opacity: '0.3',
                                             // fontSize: '0px',
                                         }}
                                    >
                                        {/*{'1234567890'.repeat(10000)}*/}

                                        <div className={styles.selectable}
                                             style={{
                                                 // color: 'white',
                                                 position: 'absolute',
                                                 width: `${groupFrame.width}px`,
                                                 height: `${groupFrame.height}px`,
                                                 fontSize: '10px',
                                                 // whiteSpace: 'nowrap',
                                                 // overflowWrap: 'break-word',
                                                 overflow: 'hidden',
                                                 textOverflow: 'clip',
                                             }}>{'1234567890'.repeat(1000)}
                                        </div>

                                    </div>
                                }
                                {/*==========================================================*/}

                                {(!groupMoveContainerId || id !== groupMoveContainerId) &&
                                    <BvLinearEvent id={id}
                                                   primaryText={container.primaryText}
                                                   secondaryText={container.secondaryText}
                                                   tagText={container.tagText}
                                                   objectType={container.objectType}
                                                   objectEpisodeInfo={container.objectEpisodeInfo}
                                                   objectDuration={`${Math.trunc(container.objectDuration / 60)}m`}
                                                   height={container.objectDuration * tableCellYpx / (viewTimeLineSize * 60)}
                                                   width={initData[id] ? initData[id].width : 0}
                                                   color={container.color}
                                                   selected={selectedContainersList[id]}
                                        // containersSelectable={props.containersSelectable}
                                    />
                                }

                            </div>

                        </Draggable>
                    )

                })

            })
        }
    })


    return (
        <>
            {tableContainers}

            {groupMoveContainerId &&
                <div style={{
                    position: 'absolute',
                    top: `${initData[groupMoveContainerId].xy.y}px`,
                    left: `${initData[groupMoveContainerId].xy.x}px`,
                    // zIndex: '3000',
                }}>
                    <BvLinearEvent id={'groupMoveContainerId'}
                                   primaryText={containersList[groupMoveContainerId].primaryText}
                                   secondaryText={containersList[groupMoveContainerId].secondaryText}
                                   tagText={containersList[groupMoveContainerId].tagText}
                                   objectType={containersList[groupMoveContainerId].objectType}
                                   objectEpisodeInfo={containersList[groupMoveContainerId].objectEpisodeInfo}
                                   objectDuration={`${Math.trunc(containersList[groupMoveContainerId].objectDuration / 60)}m`}
                                   height={containersList[groupMoveContainerId].objectDuration * tableCellYpx / (viewTimeLineSize * 60)}
                                   width={initData[groupMoveContainerId] ? initData[groupMoveContainerId].width : 0}
                                   color={containersList[groupMoveContainerId].color}
                                   selected={selectedContainersList[groupMoveContainerId]}
                        // containersSelectable={props.containersSelectable}
                    />
                </div>
            }

            {/*                         */}
            {/* Containers context menu */}
            {/*                         */}
            <ContextMenu active={contextMenus[ContextMenusNames.CONTAINER_CONTEXT_MENU]}
                         top={contextMenuCoords[1]}
                         left={contextMenuCoords[0]}
            >

                {selectedContainersList[containerIdForDelete]
                    ? <div onClick={onClickContextMenuDeleteSelected}
                           onMouseDown={e => e.stopPropagation()}
                    > Delete selected </div>
                    : <div onClick={onClickContextMenuDelete}
                           onMouseDown={e => e.stopPropagation()}
                    > Delete </div>
                }

                <div>
                    other...
                </div>

            </ContextMenu>
        </>
    )
}