import React, {useEffect, useRef} from 'react'
import styles from './ScrollLine.module.scss'
import {useDispatch, useSelector} from 'react-redux';
import {loadTableContentParamsAC} from '../../../00_redux/actions';
import {AppRootStateType} from '../../../../01_redux/store';
import {TableContentParamsType} from '../../../00_redux/types';

export const ScrollLine: React.FC = () => {
    const dispatch = useDispatch()

    const scrollRef = useRef<HTMLDivElement>(null)

    const {
        contentWidth,
        scrollLeft,
    } = useSelector<AppRootStateType, TableContentParamsType>(state => state.scheduler.tableContentParams)

    const viewSidePanel = useSelector<AppRootStateType>(state => state.scheduler.viewSidePanel)

    useEffect(() => {
        scrollRef.current!.scrollLeft = scrollLeft
    }, [scrollLeft])


    return (
        // <div className={styles.main}>
        <div className={`${styles.main} ${viewSidePanel ? styles.withSidePanel : styles.withoutSidePanel}`}>
            <div className={styles.left}></div>

            <div className={styles.scrollLine}
                 ref={scrollRef}
                 onScroll={(e) =>
                     dispatch(loadTableContentParamsAC({scrollLeft: e.currentTarget.scrollLeft} as TableContentParamsType))}
            >
                <div className={styles.content}
                     style={{width: `${contentWidth}px`}}>
                </div>
            </div>

            <div className={styles.right}></div>
            <div className={styles.gap}></div>
        </div>
    )
}