import React from 'react'
import styles from './SidePanel.module.scss'
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../01_redux/store';
import {LoadedContainersType} from '../../00_redux/types';
import {setDragStartSidePanelAC} from '../../00_redux/actions';

export const SidePanel: React.FC = () => {
    const dispatch = useDispatch()

    const loadedContainersList = useSelector<AppRootStateType, LoadedContainersType>(state => state.scheduler.loadedContainersList)

    return (
        <div className={styles.main}
             style={{overflowY: 'scroll'}}
        >


            <div style={{display: 'flex', flexDirection: 'column'}}>
                {
                    Object.keys(loadedContainersList).map(id =>

                        <div key={id}
                             style={{
                                 border: '1px solid red',
                                 height: '40px',
                                 display: 'flex',
                                 alignItems: 'center',
                                 position: 'relative',
                             }}>

                            <div draggable={true}
                                 onDragStart={()=>{
                                     dispatch(setDragStartSidePanelAC(true, id))
                                 }}
                                 onDragEnd={()=>dispatch(setDragStartSidePanelAC(false, ''))}
                                 style={{
                                     display: 'inline-block',
                                     border: '1px solid green',
                                     // position: 'absolute',
                                     // top: '0',
                                     // left: '0',
                                     width: '100px',
                                 }}>
                                {loadedContainersList[id].primaryText}
                            </div>
                        </div>
                    )

                }
            </div>


        </div>

    )
}


