import React from 'react'
import styles from './TimeLine.module.scss'
import {useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../../01_redux/store';
import {TimeLineCell} from './01_TimeLineCell/TimeLineCell';


export const TimeLine: React.FC = () => {
    const viewTimeLineSize = useSelector<AppRootStateType, number>(state => state.scheduler.viewTimeLineSize)

    return (
        <div className={styles.main}>
            {
                [...Array(24 * 60 / viewTimeLineSize)].map((elem, index) =>
                    <TimeLineCell key={index}/>)
            }
        </div>
    )
}