import React, {MouseEvent} from 'react';
import styles from './DaysLineCell.module.scss'
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../../../01_redux/store';
import {TableContentParamsType} from '../../../../../00_redux/types';
import {chooseContainersByDateAC} from '../../../../../00_redux/actions';

type DaysLineCellType = {
    month: string
    day: string
    dayOfWeek: string
    date: string
}

export const DaysLineCell: React.FC<DaysLineCellType> = (
    {
        month,
        day,
        dayOfWeek,
        date,
    }) => {

    const dispatch = useDispatch()

    const {
        tableCellXpx,
    } = useSelector<AppRootStateType, TableContentParamsType>(state => state.scheduler.tableContentParams)

    const monthStyle = month
        ? styles.borderCell
        : ''


    const onClickDayHandler = (e: MouseEvent<HTMLDivElement>) => {
        e.stopPropagation()
        dispatch(chooseContainersByDateAC(e.ctrlKey, date))
    }


    return (
        <div className={styles.main}
             style={{width: `${tableCellXpx}px`}}
        >
            <div className={styles.month + ' ' + monthStyle}
            >
                {month}
            </div>
            <div className={styles.dayWeek}
                 onClick={onClickDayHandler}
            >
                <div className={styles.day}>{day}</div>
                <div className={styles.week}>{dayOfWeek}</div>
            </div>
        </div>
    )
}