import React from 'react'
import styles from './ContextMenu.module.scss'

type ContextMenuPropsType = {
    active: boolean
    top?: number
    left?: number
}

export const ContextMenu: React.FC<ContextMenuPropsType> = (props) => {
    if (props.active) {
        return (
            <div className={styles.main}>
                <div className={styles.contextMenu}
                     style={{
                         top: `${props.top}px`,
                         left: `${props.left}px`,
                     }}
                     onContextMenu={(e) => e.preventDefault()}
                >
                    {props.children}
                </div>
            </div>
        )
    }

    return <></>
}