import React from 'react';
import { BvLabel } from '../bvLabel';
import {
    IconSearch,
    IconMail,
    IconCircleX,
    IconAlertCircle,
} from '@tabler/icons';

export interface BvInputProps {
    id: string;
    name?: string;
    label: string;
    hideLabel?: boolean
    type: string;
    disabled?: boolean;
    required?: boolean;
    autoComplete?: string;
    autoFocus?: boolean;
    placeholder?: string;
    defaultValue?: string;
    value?: any;
    onBlur?: (e: React.FocusEvent<HTMLInputElement>) => void;
    onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

export const BvInput = React.forwardRef(
    (
        {
            id,
            name,
            label,
            hideLabel,
            type,
            disabled,
            required,
            autoComplete,
            autoFocus,
            placeholder,
            defaultValue,
            value,
            onBlur,
            onChange,
        }: BvInputProps,
        ref: any
    ) => {
        const [data, setData] = React.useState(value);
        const isLeftIcon = type === 'search' || type === 'email';
        const isRightIcon = !(
            type === 'date' ||
            type === 'time' ||
            type === 'datetime-local' ||
            type === 'password' ||
            type === 'text' ||
            type === 'number'
        );

        const handleClear = () => {
            setData('');
        };

        return (
            <div className="flex flex-col">
                <BvLabel htmlFor={id} label={label} hideLabel={hideLabel} />
                <div className="relative">
                    {/* Start Icon */}
                    {type === 'search' && (
                        <IconSearch
                            width={40}
                            height={40}
                            className="absolute inset-y-0 left-0 flex items-center px-2 pointer-events-none mt-auto mb-auto text-slate-900"
                        />
                    )}
                    {type === 'email' && (
                        <IconMail
                            width={40}
                            height={40}
                            className="absolute inset-y-0 left-0 flex items-center px-2 pointer-events-none mt-auto mb-auto"
                        />
                    )}
                    {/* End Icon */}
                    {isRightIcon && (
                        <IconCircleX
                            width={40}
                            height={40}
                            className="absolute inset-y-0 right-0 flex items-center px-2 mt-auto mb-auto cursor-pointer"
                            onClick={handleClear}
                        />
                    )}
                    {type === 'password' && (
                        <IconAlertCircle
                            width={40}
                            height={40}
                            className="absolute inset-y-0 right-0 flex items-center px-2 mt-auto mb-auto cursor-pointer"
                            onClick={() => {
                                alert('This is an alert message');
                            }}
                        />
                    )}
                    <input
                        ref={ref}
                        id={id}
                        name={name}
                        type={type}
                        aria-label={label}
                        disabled={disabled}
                        required={required}
                        autoComplete={autoComplete}
                        autoFocus={autoFocus}
                        className={`peer bv-form-item-input w-full
            ${isLeftIcon ? 'pl-10 ' : 'pl-2'}
            ${isRightIcon ? 'pr-10 ' : 'pr-2'}

                        text-base focus:shadow-outline focus:border-2 focus:border-sky-500 ${disabled ? 'bg-slate-200' : ''
                            }`}
                        placeholder={placeholder}
                        value={data}
                        defaultValue={defaultValue}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                </div>
                <div className="hidden peer-invalid:block text-red-500 text-sm">
                    Error text goes here
                </div>
            </div>
        );
    }
);
