import React from 'react'
import styles from './TableBody.module.scss'
import {Columns} from './02_Columns/Columns';
import {TimeLine} from './01_TimeLine/TimeLine';
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../01_redux/store';
import {setContextMenuAC} from '../../../00_redux/actions';

export const TableBody: React.FC = () => {
    const dispatch = useDispatch()
    const viewSidePanel = useSelector<AppRootStateType, boolean>(state => state.scheduler.viewSidePanel)
    const containersDragMode = useSelector<AppRootStateType, boolean>(state => state.scheduler.containersDragMode)

    return (
        // <div className={styles.main}>
        <div className={`${styles.main} ${viewSidePanel ? styles.withSidePanel : styles.withoutSidePanel}`}
             style={{cursor: `${containersDragMode ? 'move' : 'default'}`}}
             onScroll={() => dispatch(setContextMenuAC('', false))}
        >
            <TimeLine/>
            <Columns/>
            <TimeLine/>
        </div>
    )
}