import React from 'react';

import { IconX } from '@tabler/icons';
import * as Dialog from '@radix-ui/react-dialog';
import { DialogContent, DialogDescription, DialogOverlay, DialogPortal, DialogTitle } from '@radix-ui/react-dialog';

export interface BvDialogProps {
    id: string;
    buttonLabel: string;
    dialogLabel?: string;
    dialogDescription: string;
    dialogContent: any;
    dialogCloseTitle?: string;
}

function Content({ children, ...props }: any) {
    return (
        <DialogPortal className='relative'>
            <DialogOverlay className='bg-black fixed inset-0 opacity-25' />
            <DialogContent className='fixed top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 bg-slate-50 rounded-lg max-w-xl max-h-96 p-7 shadow-lg' {...props}>{children}</DialogContent>
        </DialogPortal>
    );
}

export const BvDialog = React.forwardRef(({
    id,
    buttonLabel,
    dialogLabel,
    dialogDescription,
    dialogContent,
    dialogCloseTitle
}: BvDialogProps, ref: any) => {
    return (
        <div id={id} ref={ref}>
            <Dialog.Root>
                <Dialog.Trigger asChild className='btn-primary'>
                    <button>{buttonLabel}</button>
                </Dialog.Trigger>
                <Content>
                    <DialogTitle className='font-semibold text-lg'>{dialogLabel ?? buttonLabel}</DialogTitle>
                    <DialogDescription className='text-slate-900'>
                        {dialogDescription}
                    </DialogDescription>
                    {dialogContent}
                    <div className='flex justify-end mt-3'>
                        <Dialog.Close asChild>
                            <button aria-label="Close" className='btn-primary'>
                                {dialogCloseTitle || 'Save changes'}
                            </button>
                        </Dialog.Close>
                    </div>
                    <Dialog.Close asChild>
                        <button className=' absolute top-5 right-5 p-1 rounded-full hover:bg-slate-300'>
                            <IconX />
                        </button>
                    </Dialog.Close>
                </Content>
            </Dialog.Root>
        </div>
    )
})

