import React from "react";
import * as LabelPrimitive from "@radix-ui/react-label";

export interface BvLabelProps {
  htmlFor: string;
  label: string;
  hideLabel?: boolean;
  layout?: string | "horizontal" | "vertical";
}

export const BvLabel = React.forwardRef(({
  htmlFor,
  label,
  hideLabel,
  layout
}: BvLabelProps, ref: any) => {
  return (
    <LabelPrimitive.Root
      ref={ref}
      htmlFor={htmlFor}
      className={` ${hideLabel ? "sr-only" : "py-1"} ${layout === "horizontal" ? "inline" : "block"} rtl:text-right`}
    >
      {label}
    </LabelPrimitive.Root>
  );
})
