import React from 'react'
import styles from './TableHead.module.scss'
import {TimeLineHead} from './01_TimeLineHead/TimeLineHead';
import {DaysLine} from './02_DaysLine/DaysLine';
import {useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../01_redux/store';

export const TableHead: React.FC = () => {
    const viewSidePanel = useSelector<AppRootStateType>(state => state.scheduler.viewSidePanel)

    return (
        // <div className={styles.main}>
        <div className={`${styles.main} ${viewSidePanel ? styles.withSidePanel : styles.withoutSidePanel}`}>
            <TimeLineHead/>
            <DaysLine/>
            <TimeLineHead/>

            <div className={styles.gap}></div>
        </div>
    )
}