import React from 'react'
import styles from './Toolbar.module.scss'
import {View} from './01_View/View';
import {Docking} from './02_Docking/Docking';
import {LinearScheduler} from './01_LinearScheduler/LinearScheduler';


export const Toolbar: React.FC = () => {
    return (
        <div className={styles.main}>
            <div className={styles.newToolbar}>
                <LinearScheduler/>
            </div>

            <div className={styles.oldToolbar}>
                <View/>

                <div style={{
                    background: 'aquamarine',
                    display: 'grid',
                    alignContent: 'center',
                    justifyContent: 'center',
                }}>Some else
                </div>

                <Docking/>
            </div>
        </div>
    )
}