import React, {DragEvent, useEffect, useRef, useState} from 'react'
import styles from './Columns.module.scss'
import {useDispatch, useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../../01_redux/store';
import {
    addNewContainerAC, chooseContainersBySelectWindowAC,
    loadTableContentParamsAC,
    setContextMenuAC,
    unselectAllContainersAC
} from '../../../../00_redux/actions';
import {
    ContextMenusNames,
    ContextMenusType,
    DaysListType,
    LoadedContainersType, SelectWindowCoordsType,
    TableContentParamsType,
    ViewDaysOfWeekType
} from '../../../../00_redux/types';
import {generateDaysListTC, initGroupedContainersListTC} from '../../../../00_redux/thunks';
import {InitDataTypeTableContainers, TableContainers} from '../03_TableContainers/TableContainers';
import {TableGrid} from '../04_TableGrid/TableGrid';


const MIN_CELL_WIDTH = 100
const MAX_CELL_WIDTH = 500

const MAIN_BUTTON = 0 // Left mouse button - in normal mode

export const Columns: React.FC = () => {
    const dispatch = useDispatch()

    const tableRef = useRef<HTMLDivElement>(null)

    const [windowWidth, setWindowWidth] = useState(0)

    const {
        contentWidth,
        scrollLeft,
        tableCellXpx,
        tableCellYpx,
    } = useSelector<AppRootStateType, TableContentParamsType>(state => state.scheduler.tableContentParams)

    const viewTimeLineSize = useSelector<AppRootStateType, number>(state => state.scheduler.viewTimeLineSize)
    const viewDays = useSelector<AppRootStateType, number>(state => state.scheduler.viewDays)
    const viewDaysOfWeek = useSelector<AppRootStateType, ViewDaysOfWeekType>(state => state.scheduler.viewDaysOfWeek)
    const beginDate = useSelector<AppRootStateType, string>(state => state.scheduler.beginDate)
    const daysList = useSelector<AppRootStateType, DaysListType>(state => state.scheduler.daysList)

    const loadedContainersList = useSelector<AppRootStateType, LoadedContainersType>(state => state.scheduler.loadedContainersList)
    const viewSidePanel = useSelector<AppRootStateType, boolean>(state => state.scheduler.viewSidePanel)
    const dragSidePanel = useSelector<AppRootStateType, boolean>(state => state.scheduler.dragStartSidePanel)
    const draggedContainerId = useSelector<AppRootStateType, string>(state => state.scheduler.draggedContainerId)
    const contextMenus = useSelector<AppRootStateType, ContextMenusType>(state => state.scheduler.contextMenus)


    // console.log('load state:', contentWidth, tableCellXpx, daysList)

    // ==================================================================================
    //
    //   VIEW WIDTH CHANGES
    //
    const onWindowResize = () => {
        setWindowWidth(window.innerWidth)
    }

    useEffect(() => {
        window.addEventListener('resize', onWindowResize)
        return () => window.removeEventListener('resize', onWindowResize)
    }, [])

    useEffect(() => {
        let newTableCellXpx = 0
        const viewWidth = tableRef.current!.clientWidth
        if (daysList.length) {
            newTableCellXpx = Math.trunc(viewWidth / daysList.length)

            if (newTableCellXpx < MIN_CELL_WIDTH) {
                newTableCellXpx = MIN_CELL_WIDTH
            } else if (newTableCellXpx > MAX_CELL_WIDTH) {
                newTableCellXpx = MAX_CELL_WIDTH
            }
        }
        const newContentWidth = newTableCellXpx * daysList.length

        // console.log('effect state:', contentWidth, tableCellXpx, daysList)
        // console.log('effect new:', newContentWidth, newTableCellXpx, daysList)
        dispatch(loadTableContentParamsAC({
            viewWidth,
            contentWidth: newContentWidth,
            tableCellXpx: newTableCellXpx
        } as TableContentParamsType))
    }, [windowWidth, daysList, viewSidePanel])
    // ==================================================================================


    // ==================================================================================
    //
    // SCROLL
    //
    useEffect(() => {
        tableRef.current!.scrollLeft = scrollLeft
        dispatch(setContextMenuAC('', false))
    }, [scrollLeft])
    // ==================================================================================


    // ==================================================================================
    //
    // GENERATE DAYS LIST
    //
    useEffect(() => {
        dispatch(generateDaysListTC())
    }, [beginDate, viewDays, viewDaysOfWeek])
    // ==================================================================================


    // ==================================================================================
    const [dragCoords, setDragCoords] = useState<Array<number>>([-1, -1])

    const onDragOverHandler = (e: DragEvent<HTMLDivElement>) => {
        // console.log('dragOver', e.target)
        // e.stopPropagation()
        e.preventDefault()

        // console.log(e.clientX - e.currentTarget.getBoundingClientRect().left)
        const x = Math.trunc((e.clientX - e.currentTarget.getBoundingClientRect().left) / tableCellXpx) * tableCellXpx

        const contentHeight = 24 * 60 * tableCellYpx / viewTimeLineSize
        const dragContainerHeight = loadedContainersList[draggedContainerId].objectDuration / 60 * tableCellYpx / viewTimeLineSize
        const yScreen = Math.trunc((e.clientY - e.currentTarget.getBoundingClientRect().top) / tableCellYpx) * tableCellYpx
        const y = (yScreen + dragContainerHeight) < contentHeight
            ? yScreen
            : contentHeight - dragContainerHeight

        if (x !== dragCoords[0] || y !== dragCoords[1]) {
            setDragCoords([x, y])
        }
    }

    const onDragLeaveHandler = (e: DragEvent<HTMLDivElement>) => {
        // console.log('dragLeave', e.target)
        setDragCoords([-1, -1])
    }

    const onDropHandler = (e: DragEvent<HTMLDivElement>) => {
        dispatch(addNewContainerAC(draggedContainerId,
            loadedContainersList[draggedContainerId],
            daysList[dragCoords[0] / tableCellXpx],
            viewTimeLineSize * dragCoords[1] / tableCellYpx,
            1))

        setDragCoords([-1, -1])

        dispatch(initGroupedContainersListTC())
    }
    // ==================================================================================


    // ==================================================================================
    const [initDataForColumns, setInitDataForColumns] = useState<InitDataTypeTableContainers>({})

    const [startSelectWindowCoords, setStartSelectWindowCoords] = useState<SelectWindowCoordsType>({x: -1, y: -1})
    const [currentSelectWindowCoords, setCurrentSelectWindowCoords] = useState<SelectWindowCoordsType>({x: -1, y: -1})

    const [selectWindowShowed, setSelectWindowShowed] = useState<boolean>(false)

    const [containersSelectable, setContainersSelectable] = useState<boolean>(true)

    // ==================================================================================


    return (
        <div className={styles.main}
             ref={tableRef}

             onMouseDown={(e) => {
                 setSelectWindowShowed(false)

                 if (e.button === MAIN_BUTTON) {
                     setContainersSelectable(false)

                     if (e.shiftKey) {
                         // e.stopPropagation()

                         console.log('StartSelectWindow = begin', e.clientX - e.currentTarget.getBoundingClientRect().left, e.clientY - e.currentTarget.getBoundingClientRect().top)

                         if (!e.ctrlKey) {
                             dispatch(unselectAllContainersAC())
                         }

                         setStartSelectWindowCoords({
                             x: scrollLeft + e.clientX - e.currentTarget.getBoundingClientRect().left,
                             y: e.clientY - e.currentTarget.getBoundingClientRect().top,
                         })
                     }
                 }
             }}

             onMouseMove={(e) => {
                 if (e.button === MAIN_BUTTON && startSelectWindowCoords.x !== -1) {
                     // e.stopPropagation()
                     // e.preventDefault()

                     setSelectWindowShowed(true)

                     setCurrentSelectWindowCoords({
                         x: scrollLeft + e.clientX - e.currentTarget.getBoundingClientRect().left,
                         y: e.clientY - e.currentTarget.getBoundingClientRect().top
                     })
                 }
             }}

             onMouseUp={(e) => {
                 if (e.button === MAIN_BUTTON && startSelectWindowCoords.x !== -1 && currentSelectWindowCoords.x !== -1) {
                     // e.stopPropagation()
                     console.log('StartSelectWindow = end')

                     // setSelectWindowShowed(false)

                     dispatch(chooseContainersBySelectWindowAC(e.ctrlKey, startSelectWindowCoords, currentSelectWindowCoords, initDataForColumns))
                 }

                 setContainersSelectable(true)
                 setStartSelectWindowCoords({x: -1, y: -1})
                 setCurrentSelectWindowCoords({x: -1, y: -1})
             }}

             onClick={(e) => {
                 console.log('Click, ', 'selectWindowShowed=', selectWindowShowed)
                 if (selectWindowShowed) {
                     e.stopPropagation()
                     // e.preventDefault()

                     setSelectWindowShowed(false)
                     console.log('Click stopped!')
                 }
             }}
        >

            {/*               */}
            {/* Select window */}
            {/*               */}
            {(!contextMenus[ContextMenusNames.CONTAINER_CONTEXT_MENU] &&
                    (startSelectWindowCoords.x !== -1 && currentSelectWindowCoords.x !== -1)) &&
                <div className={styles.selectWindowBody}
                     style={{
                         top: `${Math.min(startSelectWindowCoords.y, currentSelectWindowCoords.y)}px`,
                         left: `${Math.min(startSelectWindowCoords.x, currentSelectWindowCoords.x)}px`,
                         height: `${Math.abs(startSelectWindowCoords.y - currentSelectWindowCoords.y)}px`,
                         width: `${Math.abs(startSelectWindowCoords.x - currentSelectWindowCoords.x)}px`,
                     }}
                >
                </div>
            }


            {/*               */}
            {/* Area for drop */}
            {/*               */}
            {dragSidePanel && <div className={styles.dragZone}
                                   style={{
                                       width: `${contentWidth}px`,
                                       height: `${24 * 60 / viewTimeLineSize * tableCellYpx}px`,
                                       zIndex: '100',
                                   }}
                                   onDragOver={onDragOverHandler}
                                   onDragLeave={onDragLeaveHandler}
                                   onDrop={onDropHandler}
            >
            </div>}


            <div className={styles.content}
                 style={{width: `${contentWidth}px`}}
                // onScroll={(e) =>
                //     dispatch(loadTableContentParamsAC({scrollLeft: e.currentTarget.scrollLeft} as TableContentParamsType))}
                // onMouseMove={(e)=>console.log(e.clientX,e.clientY)}
            >
                <TableGrid dragCoords={dragCoords}/>

                <TableContainers
                    setInitDataForColumns={setInitDataForColumns}
                    containersSelectable={containersSelectable}/>
            </div>
        </div>
    )
}