import {ContainerListType, ContextMenusType, SchedulerStateType} from './types';
import {initialState} from './state';
import {Actions, SchedulerActionsType} from './actions';
import moment from 'moment';


export const schedulerReducer = (state: SchedulerStateType = initialState, action: Actions): SchedulerStateType => {
    switch (action.type) {
        case SchedulerActionsType.LOAD_TABLE_CONTENT_PARAMS: {
            return {
                ...state,
                tableContentParams: {
                    ...state.tableContentParams,
                    ...action.params,
                },
            }
        }


        case SchedulerActionsType.SET_BEGIN_DATE: {
            return {
                ...state,
                beginDate: action.date,
            }
        }


        case SchedulerActionsType.SET_ACTIVE_CHANNEL: {
            return {
                ...state,
                activeChannelId: action.channelId,
            }
        }


        case SchedulerActionsType.SET_DAYS_LIST: {
            return {
                ...state,
                daysList: action.days,
            }
        }


        case SchedulerActionsType.SET_VIEW_TIME: {
            return {
                ...state,
                viewTimeLineSize: action.time,
            }
        }

        case SchedulerActionsType.SET_VIEW_DAYS: {
            return {
                ...state,
                viewDays: action.days,
            }
        }

        case SchedulerActionsType.SET_VIEW_DAYS_OF_WEEK: {
            // const newArray = [...state.viewDaysOfWeek]
            // if (action.dayOfWeek >= 0 && action.dayOfWeek <= 6) {
            //     newArray[action.dayOfWeek] = action.value
            // }

            return {
                ...state,
                // viewDaysOfWeek: newArray,
                viewDaysOfWeek: action.daysOfWeek,
            }
        }


        case SchedulerActionsType.MOVE_CONTAINER_TO_NEW_POSITION: {
            const container = state.containersList[action.containerId]

            return {
                ...state,
                containersList: {
                    ...state.containersList,
                    [action.containerId]: {
                        ...container,
                        logDate: action.newDate,
                        startTime: action.newTime
                    }
                },
            }
        }


        case SchedulerActionsType.INIT_GROUPED_CONTAINER_LIST: {
            return {
                ...state,
                groupedContainersList: action.groupedContainersList,
            }
        }


        case SchedulerActionsType.SET_VIEW_SIDE_PANEL: {
            return {
                ...state,
                viewSidePanel: action.value,
            }
        }


        case SchedulerActionsType.SET_DRAG_START_SIDE_PANEL: {
            return {
                ...state,
                dragStartSidePanel: action.value,
                draggedContainerId: action.draggedContainerId,
            }
        }


        case SchedulerActionsType.ADD_NEW_CONTAINER: {
            return {
                ...state,
                containersList: {
                    ...state.containersList,
                    [action.containerId + '_' + Date.now()]: {
                        ...action.container,
                        logDate: action.date,
                        startTime: action.time,
                        chanelID: action.chanelID,
                    }
                },
            }
        }

        case SchedulerActionsType.DELETE_CONTAINER: {
            const newContainersList = {...state.containersList}
            delete newContainersList[action.containerId]

            return {
                ...state,
                containersList: newContainersList,
            }
        }

        case SchedulerActionsType.DELETE_CONTAINERS_GROUP: {
            const newContainersList = {...state.containersList}
            Object.keys(action.containersGroup).forEach(containerId =>
                delete newContainersList[containerId]
            )

            return {
                ...state,
                containersList: newContainersList,
                selectedContainersList: {},
            }
        }


        case SchedulerActionsType.SET_CONTEXT_MENU: {
            const cleanContextMenus: ContextMenusType = {}
            Object.keys(state.contextMenus).forEach(objKey => cleanContextMenus[objKey] = false)

            const newContextMenus = action.contextMenuName === ''
                ? cleanContextMenus
                : {
                    ...cleanContextMenus,
                    [action.contextMenuName]: action.value,
                }

            return {
                ...state,
                contextMenus: newContextMenus,
            }
        }


        case SchedulerActionsType.UNSELECT_ALL_CONTAINERS: {
            return {
                ...state,
                selectedContainersList: {},
            }
        }

        case SchedulerActionsType.SET_CONTAINER_SELECTION: {
            const newSelectedContainersList = {...state.selectedContainersList}
            if (action.value === true) {
                newSelectedContainersList[action.containerId] = true
            } else {
                delete newSelectedContainersList[action.containerId]
            }

            return {
                ...state,
                selectedContainersList: newSelectedContainersList,
            }
        }

        case SchedulerActionsType.CHOOSE_CONTAINERS_BY_SELECT_WINDOW: {
            const newSelectedContainersList = action.ctrlKey
                ? {...state.selectedContainersList}
                : {}

            const windowTop = Math.min(action.startSelectWindowCoords.y, action.currentSelectWindowCoords.y)
            const windowBottom = Math.max(action.startSelectWindowCoords.y, action.currentSelectWindowCoords.y)
            const windowLeft = Math.min(action.startSelectWindowCoords.x, action.currentSelectWindowCoords.x)
            const windowRight = Math.max(action.startSelectWindowCoords.x, action.currentSelectWindowCoords.x)

            // console.log('Reducer - select window:', windowTop, windowLeft, windowBottom, windowRight)

            Object.keys(action.initData).forEach(containerId => {
                const containerTop = action.initData[containerId].xy.y
                const containerBottom = action.initData[containerId].xy.y +
                    state.containersList[containerId].objectDuration / 60 * state.tableContentParams.tableCellYpx / state.viewTimeLineSize
                const containerLeft = action.initData[containerId].xy.x
                const containerRight = action.initData[containerId].xy.x + action.initData[containerId].width

                if (containerRight < windowLeft ||
                    containerLeft > windowRight ||
                    containerBottom < windowTop ||
                    containerTop > windowBottom) {
                    //
                    // container OUT of window
                    //

                    // delete newSelectedContainersList[containerId]
                } else {
                    //
                    // container IN window
                    //

                    newSelectedContainersList[containerId] = true
                }
            })

            // console.log('Reducer - select window:', newSelectedContainersList)

            return {
                ...state,
                selectedContainersList: newSelectedContainersList,
            }
        }

        case SchedulerActionsType.CHOOSE_CONTAINERS_BY_DATE: {
            const newSelectedContainersList = action.ctrlKey
                ? {...state.selectedContainersList}
                : {}

            Object.keys(state.containersList).forEach(containerId => {
                if (state.containersList[containerId].logDate === action.date) {
                    newSelectedContainersList[containerId] = true
                }
            })

            return {
                ...state,
                selectedContainersList: newSelectedContainersList,
            }
        }


        case SchedulerActionsType.SET_CONTAINERS_DRAG_MODE: {
            return {
                ...state,
                containersDragMode: action.value,
            }
        }


        case SchedulerActionsType.CHANGE_DATE_TIME_CONTAINERS: {
            console.log('ChangeDateTime: deltaDays =', action.deltaDays, 'deltaMinutes =', action.deltaMinutes)

            const newContainersList: ContainerListType = {}
            Object.keys(action.containers).forEach(containerId => {
                const date = moment(state.containersList[containerId].logDate, 'YYYY-MM-DD').add(action.deltaDays, 'days').format('YYYY-MM-DD')

                newContainersList[containerId] = {
                    ...state.containersList[containerId],
                    logDate: date,
                    startTime: state.containersList[containerId].startTime + action.deltaMinutes,
                }
            })

            return {
                ...state,
                containersList: {
                    ...state.containersList,
                    ...newContainersList,
                },
                // selectedContainersList: {},
            }
        }


        default:
            return state
    }
}


