import React from 'react';
import { IconCheck, IconChevronDown, IconChevronUp } from '@tabler/icons';
import * as SelectPrimitive from '@radix-ui/react-select';
import { BvLabel } from '../bvLabel';

export interface BvSelectProps {
    id: string;
    label: string;
    hideLabel?: boolean;
    options: Object[];

    // options data structure prototype
    // {
    //     "fruit": [
    //         { label: "Banana", value: "banana" },
    //         { label: "Apple", value: "apple" },
    //         { label: "Orange", value: "orange" }
    //     ],
    //     "meat": [
    //         { label: "Raw Beef", value: "beefRaw" },
    //         { label: "Steak", value: "beefCut" },
    //         { label: "Pork", value: "pork" },
    //         { label: "Chicken", value: "chicken" },
    //         { label: "Lamb", value: "lamb" }
    //     ]
    // }
    value?: any;
}

export const BvSelect = React.forwardRef(({
    id,
    label,
    hideLabel,
    options,
    value,
}: BvSelectProps, ref: any) => {

    return (
        <div className="flex flex-col">
            <BvLabel htmlFor={id} label={label} hideLabel={hideLabel} />
            <div id={id} ref={ref}>
                <SelectPrimitive.Root defaultValue={value} >
                    <SelectPrimitive.SelectTrigger aria-label={label} className="bv-form-item-input inline-flex items-center justify-between w-full gap-1 px-2">
                        <SelectPrimitive.Value />
                        <SelectPrimitive.Icon>
                            <IconChevronDown size={16} />
                        </SelectPrimitive.Icon>
                    </SelectPrimitive.SelectTrigger>
                    <SelectPrimitive.Content className="overflow-hidden bg-slate-50 rounded-sm ">
                        <SelectPrimitive.SelectScrollUpButton className="flex items-center justify-center h-6 bg-slate-50 cursor-default">
                            <IconChevronUp size={16} />
                        </SelectPrimitive.SelectScrollUpButton>
                        <SelectPrimitive.SelectViewport className="p-1">
                            {options && Object.keys(options).map((key, index) => {
                                return (
                                    <span key={index}>
                                        <SelectPrimitive.SelectGroup>
                                            <SelectPrimitive.Label className="px-6 text-sm  text-slate-500 capitalize">{key}</SelectPrimitive.Label>
                                            {
                                                (options[key as any] as Array<any>).map((option: any, optionIndex: any) => {
                                                    return (
                                                        <SelectPrimitive.SelectItem value={option['value']} key={optionIndex} className="text-base rounded-sm leading-none  flex items-center h-6 pl-6 pr-9 py-0 relative select-none focus:shadow-none focus:outline-none focus:ring focus:ring-sky-400/50 focus:border-sky-500">
                                                            <SelectPrimitive.SelectItemText>{option['label']}</SelectPrimitive.SelectItemText>
                                                            <SelectPrimitive.SelectItemIndicator className="absolute left-0 w-6 inline-flex items-center justify-center">
                                                                <IconCheck size={16} />
                                                            </SelectPrimitive.SelectItemIndicator>
                                                        </SelectPrimitive.SelectItem>
                                                    )
                                                })
                                            }
                                        </SelectPrimitive.SelectGroup>
                                        {(index != Object.keys(options).length - 1) && <SelectPrimitive.SelectSeparator className="h-px bg-slate-300 m-1" />}
                                    </span>
                                )
                            })
                            }
                        </SelectPrimitive.SelectViewport>
                        <SelectPrimitive.SelectScrollDownButton className="flex items-center justify-center h-6 bg-slate-50 cursor-default">
                            <IconChevronDown size={16} />
                        </SelectPrimitive.SelectScrollDownButton>
                    </SelectPrimitive.Content>
                </SelectPrimitive.Root>
            </div>
        </div>
    )
})