import React, {useEffect, useRef} from 'react'
import styles from './DaysLine.module.scss'
import {useSelector} from 'react-redux';
import {AppRootStateType} from '../../../../../01_redux/store';
import {DaysListType, TableContentParamsType} from '../../../../00_redux/types';
import {DaysLineCell} from './01_DaysLineCell/DaysLineCell';
import moment from 'moment';

export const DaysLine: React.FC = () => {
    const scrollRef = useRef<HTMLDivElement>(null)

    const daysList = useSelector<AppRootStateType, DaysListType>(state => state.scheduler.daysList)
    const {
        scrollLeft,
        tableCellXpx,
    } = useSelector<AppRootStateType, TableContentParamsType>(state => state.scheduler.tableContentParams)

    // const viewSidePanel = useSelector<AppRootStateType>(state => state.scheduler.viewSidePanel)

    useEffect(() => {
        scrollRef.current!.scrollLeft = scrollLeft
    }, [scrollLeft])


    const sameMonth = (date: string, index: number) => {
        const month1 = moment(date, 'YYYY-MM-DD').format('MMMM')
        if (index === 0) {
            return month1
        }

        const month2 = moment(daysList[index - 1], 'YYYY-MM-DD').format('MMMM')
        return month1 === month2
            ? ''
            : month1
    }


    const daysLineCells = daysList.map((date, index) =>
        <DaysLineCell key={date}
                      month={sameMonth(date, index)}
                      day={moment(date, 'YYYY-MM-DD').format('D')}
                      dayOfWeek={moment(date, 'YYYY-MM-DD').format('dddd')}
                      date={date}
        />
    )


    return (
        <div className={styles.main}
        // <div className={`${styles.main} ${viewSidePanel ? styles.withSidePanel : styles.withoutSidePanel}`}

        ref={scrollRef}
             // onScroll={(e) =>
             //     dispatch(loadTableContentParamsAC({scrollLeft: e.currentTarget.scrollLeft} as TableContentParamsType))}
        >
            <div className={styles.content}
                 style={{width: `${tableCellXpx * daysList.length}px`}}>

                {daysLineCells}

            </div>
        </div>
    )
}


