import React from 'react';
import './App.scss';
import {AppRoutes} from './01_AppRoutes/AppRoutes';
import {useDispatch} from 'react-redux';
import {setContextMenuAC, unselectAllContainersAC} from '../03_Scheduler/00_redux/actions';


function App() {
    const dispatch = useDispatch()

    const removeHandler = () => {
        dispatch(setContextMenuAC('', false))
        dispatch(unselectAllContainersAC())
    }

    return (
        <div className={'app'}
             onContextMenu={removeHandler}
             onClick={removeHandler}
             onDragStart={removeHandler}
        >
            <div className={'main'}>
                <div className={'sidebar'}>
                    sidebar
                </div>

                <div className={'content'}>
                    <div className={'top'}>
                        top
                    </div>

                    <div className={'tabs'}>
                        tabs
                    </div>

                    <div className={'body'}>
                        <AppRoutes/>
                    </div>
                </div>

            </div>
            <div className={'statusbar'}>
                {/*statusbar*/}
            </div>
        </div>
    )
}

export default App;